import React from "react";

const Skills = (props: any) => {

  const progressBarStyles = (percentage: number) => {
    const styles: Object = {
        width: `${percentage}%`,
        height: '10px',
        transition: '.9s',
        backgroundColor: '#e60b5b',
    }
    return styles;
  }

  return (
    <section id="skills" className="skills">
      <div className="box container">
        <div className="section-title">
          <h2>Skills</h2>
          <p>
            En esta sección muestro un resumen general de mis habilidades y/o tecnologías dominadas. 
            Un 100% significa que domino la tecnología a un buen nivel (no indica grado de expertise en sí, es sólo referencial).
          </p>
        </div>

        <div className="row">
          <div className="col-lg-6" data-aos={"fade-up"}>

            <div className="progress">
              <span className="skill">
                Python <i className="val">90%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(90)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                React.js <i className="val">80%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(80)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                TypeScript <i className="val">75%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(75)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                Java <i className="val">70%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(70)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                GIT <i className="val">70%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(70)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                SQL Server <i className="val">55%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(55)}></div>
              </div>
            </div>

          </div>
          <div className="col-lg-6" data-aos={"fade-up"}>

            <div className="progress">
              <span className="skill">
                JavaScript <i className="val">85%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(85)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                HTML y CSS <i className="val">75%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(75)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                PostgreSQL <i className="val">70%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(70)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                Node.js (Express) <i className="val">70%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(70)}></div>
              </div>
            </div>

            <div className="progress">
              <span className="skill">
                Android Nativo (Java) <i className="val">60%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(60)}></div>
              </div>
            </div>
            
            <div className="progress">
              <span className="skill">
                Angular (TypeScript) <i className="val">55%</i>
              </span>
              <div className="progress-bar-wrap">
                <div style={progressBarStyles(55)}></div>
              </div>
            </div>

          </div>

        </div>
      </div>
    </section>
  );
};

export default Skills;
