import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { toastSuccess, toastError } from '../utils/Toasts';

type IFormInputs = {
    name: string,
    email: string,
    subject: string,
    comment: string
};

const Contact3 = (props: any) => {
  const { register, handleSubmit } = useForm<IFormInputs>();
  const [loading, setLoading] = useState<boolean>(false);

  const onSubmit = async (data: IFormInputs) => {
    console.log(data)
    setLoading(true);
    let results = await fetchToServer(data);
    setLoading(false);

    if (results.status >= 200 && results.status < 300) {
      toastSuccess();
    } else {
      toastError();
    }
  }

  const fetchToServer = async (data: Object) => {
    try {
        let response = await fetch(`http://localhost:5000/api/send-email`, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, *cors, same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
            'Content-Type': 'application/json'
            // 'Content-Type': 'application/x-www-form-urlencoded',
          },
          redirect: 'follow', // manual, *follow, error
          referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
          body: JSON.stringify(data) // body data type must match "Content-Type" header
        });

        return response;
    } catch (error) {
        console.log(error);
        return error;
    }
  }

  return (
    <section id="contact" className="contact">

      <div className="box container">
        <div className="section-title">
          <h2>Contacto</h2>
          <p>
            Ante cualquier consulta, no dude contactarse a través de mis RRSS indicadas en
            la barra, o mediante el siguiente formulario de contacto.
          </p>
        </div>

        <div className="row">
          <div className="col mt-3 d-flex align-items-stretch">
            <form
              onSubmit={handleSubmit(onSubmit)}
              className="info contact-form"
            >
              <div className="row">
                <div className="form-group col-md-6">
                  <label>Su nombre</label>
                  <input
                    type="text" className="form-control" {...register("name")} required
                  />
                </div>
                <div className="form-group col-md-6">
                  <label>Su correo</label>
                  <input
                    type="email" className="form-control" {...register("email")} required
                  />
                </div>
              </div>
              <div className="form-group">
                <label>Asunto</label>
                <input
                  type="text" className="form-control" {...register("subject")} required
                />
              </div>
              <div className="form-group">
                <label>Mensaje</label>
                <textarea
                  className="form-control" {...register("comment")} rows={10} required
                ></textarea>
              </div>

              <div className="my-3">
                { loading ?
                  <div className="loading">Enviando...</div>
                :
                  null }
              </div>

              <div className="text-center">
                <button type="submit">Enviar mensaje</button>
              </div>

            </form>

          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact3;
