import React from "react";

const Projects = (props: any) => {
  return (
    <section id="projects" className="projects">
      <div className="box container">
        <div className="section-title">
          <h2>Proyectos</h2>
          <p>
            En esta sección se presentan los principales proyectos que he realizado en
            el mundo TI junto a las tecnologías utilizadas en cada uno.
          </p>
        </div>

        <div className="row">
          <div className="col-lg-6" data-aos="fade-up">
            <h3 className="projects-title">Espacio Universitario</h3>
            <div className="projects-item">
              <h4>Proyecto universitario en equipo</h4>
              <h5>May 2020 - Jul 2020</h5>
              <p>
                <em>Universidad del Bío-Bío</em>
              </p>
              <p>
                Espacio Universitario nació como una idea de la necesidad de una
                plataforma de encuentro para los estudiantes universitarios en
                Chile, donde ellos puedan interactuar y compartir información
                relevante en sus contextos universitarios y formar comunidades.
              </p>
              <p>
                El proyecto consistió en elaborar un Plan de Gestión del
                Proyecto, desde un punto de vista de alcance, tiempo, costos y
                factibilidad. Luego, se programó la solución proyectada
                generando el sitio web Espacio Universitario.
              </p>
              <p className="project-tools">
                Las tecnologías utilizadas fueron las siguientes:
                <ul>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Node.js (Express)</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>React.js</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Sequelize (ORM)</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>PostgreSQL</strong>
                  </li>
                </ul>
              </p>
            </div>
          </div>
          <div className="col-lg-6">
            <h3 className="projects-title">
              Extracción de opiniones de aspectos mencionados en reseñas en
              español
            </h3>
            <div className="projects-item">
              <h4>Proyecto de Título</h4>
              <h5>Jun 2020 - Ene 2021</h5>
              <p>
                <em>Universidad del Bío-Bío</em>
              </p>
              <p>
                El proyecto consistió en implementar algoritmos para extraer
                aspectos mencionados por usuarios en reseñas o comentarios en
                idioma español, donde los usuarios opinan respecto a diferentes
                productos o ítems. Lo anterior, en la búsqueda de generar un
                recurso de valor para Sistemas de Recomendación basados en
                Aspectos. También se desarrolló una aplicación web para ejecutar
                los algoritmos y recopilar resultados.
              </p>
              <p className="project-tools">
                Las tecnologías utilizadas fueron las siguientes:
                <ul>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Python</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Flask</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>
                      Bibliotecas Python de Machine Learning (ej.: Gensim,
                      Stanza, NLTK, etc.)
                    </strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Spyder IDE</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>React.js (para app web)</strong>
                  </li>
                </ul>
              </p>

              <div className="row">
                <div className="col-auto">
                  <span style={{ fontFamily: "Poppins" }}>
                    Palabras clave —
                  </span>
                </div>
                <div className="col-auto">
                  <span className="badge-keyword">Machine Learning</span>
                </div>
                <div className="col-auto">
                  <span className="badge-keyword">Análisis de Sentimiento</span>
                </div>
                <div className="col-auto">
                  <span className="badge-keyword">Minería de Opinión</span>
                </div>
                <div className="col-auto">
                  <span className="badge-keyword">Extracción de Aspectos</span>
                </div>
              </div>
            </div>
          </div>
          <div className="col-lg-6" data-aos="fade-up">
            <h3 className="projects-title">
              Aplicación Android para control vía Wi-Fi ~ Bluetooth de Robot
              Lego NXT
            </h3>
            <div className="projects-item">
              <h4>Práctica Profesional I</h4>
              <h5>Nov 2018 - Ene 2019</h5>
              <p>
                <em>Universidad del Bío-Bío</em>
              </p>
              <p>
                El proyecto consistió en el desarrollo de una aplicación móvil
                Android nativa para control de un robot Lego NXT de 4 ruedas. El
                objetivo fue que se pudiera controlar el robot mediante un
                celular Android vía Bluetooth y vía Wi-Fi, esto último conectando vía Wi-Fi a un dispositivo Android con otro similar,
                y éste último estuviese conectado via Bluetooth al robot, para generar una conexión Maestro-Esclavo. 
                Además, se implementó la funcionalidad para que el celular Maestro (que conecta remotamente al robot) realizara visión por cámara al robot,
                para poder tocar en pantalla e indicar al robot que vaya a cierto punto.
              </p>
              <p className="project-tools">
                Las tecnologías utilizadas fueron las siguientes:
                <ul>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Java SE8</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Android Studio IDE</strong>
                  </li>
                </ul>
              </p>
            </div>
          </div>
          <div className="col-lg-6">
            <h3 className="projects-title">Reportes PDF y Excel en Node.js</h3>
            <div className="projects-item">
              <h4>Práctica Profesional II</h4>
              <h5>Nov 2020 - Ene 2021</h5>
              <p>
                <em>NovoSystem: empresa de desarrollo de software</em>
              </p>
              <p>
                El proyecto consistió en el desarrollo de funciones en Node.js para la generación de reportes en Excel y PDF,
                siendo ambas funciones parametrizadas para simplemente proveer de datos de reporte y información de columnas, cabeceras, estilos u otras esenciales,
                y con ello, obtener los archivos .XLSX y .PDF requeridos.
              </p>
              <p>
                Los reportes PDF requirieron generar distintas versiones hasta llegar a la versión final capaz de abordar distintos escenarios, y además se 
                desarrolló una plantilla HTML/EJS (JavaScript embebido para renderizado en el servidor) para dar forma al reporte PDF.
              </p>
              <p className="project-tools">
                Las tecnologías utilizadas fueron las siguientes:
                <ul>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Node.js (Sails.js)</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>Biblioteca Puppeteer (Headless Chromium)</strong>
                  </li>
                  <li>
                    <i className="fa fa-chevron-right"></i>{" "}
                    <strong>EJS (JavaScript embebido en HTML)</strong>
                  </li>
                </ul>
              </p>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Projects;
