import React, { useState } from 'react';

const Sidebar = (props: any) => {
  const [clicked, setClicked] = useState(false);

  const toggleMobile = (e: any) => {
    
    let element: HTMLElement | null = document.querySelector("body");
    if (element) {
      element.classList.toggle("mobile-nav-active");
      e.target.classList.toggle("bi-list");
      e.target.classList.toggle("bi-x"); // This will be the icon for the cancel's button
      clicked ? e.target.style.fontSize = '26px' : e.target.style.fontSize = '33px';
      setClicked(!clicked);
    }
  }
    return (
      <>
        {/* ======= Mobile nav toggle button ======= */}
        <i
          className="bi bi-list mobile-nav-toggle d-xl-none"
          onClick={(e) => toggleMobile(e)}
        ></i>

        {/* ======= Sidebar Menu ======= */}
        <div className="App-sidebar" id="sidebar">
          <div className="d-flex flex-column">

            <div className="profile">
              <img src={props.profilePicUrl} alt="" className="img-fluid"/>
              <h1 className="text-light"><a href="#">Javier Canales</a></h1>
              <div className="social-links mt-3 text-center">
                <a href="https://twitter.com/javierrcanales" target="_blank"><i className="bi bi-twitter"></i></a>
                <a href="https://facebook.com/javiercanalescastro" target="_blank"><i className="bi bi-facebook"></i></a>
                <a href="https://instagram.com/javierrcanales" target="_blank"><i className="bi bi-instagram"></i></a>
                <a href="https://github.com/javiercanales" target="_blank"><i className="bi bi-github"></i></a>
                <a href="https://linkedin.com/in/jcanalesc" target="_blank"><i className="bi bi-linkedin"></i></a>
              </div>
            </div>

            <nav id="navbar" className="nav-menu navbar">
              <ul>
                <li><a href="#home" className="nav-link"><i className="bi bi-house"></i> <span>Sobre mí</span></a></li>
                <li><a href="#projects" className="nav-link"><i className="bi bi-card-checklist"></i> <span>Proyectos</span></a></li>
                <li><a href="#portfolio" className="nav-link"><i className="bi bi-book"></i> <span>Portafolio</span></a></li>
                <li><a href="#contact" className="nav-link"><i className="bi bi-envelope"></i> <span>Contacto</span></a></li>
              </ul>
            </nav>
          </div>
        </div>
      </>
    );
}

export default Sidebar;