import { toast } from 'react-toastify';

const toastSuccess = () => {
    toast.success('¡Su mensaje fue enviado con éxito! 😸', {
        position: "top-right",
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
}

const toastError = () => {
    toast.error('Ha ocurrido un error, el envío falló... quizá el servidor está off 😿', {
        position: "top-right",
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        progress: undefined,
    });
}

export {
    toastSuccess,
    toastError
}